import './costIcon.scss';
import React from 'react';
import { withTranslation } from 'react-i18next';
const icon = require('./Cost-icon.svg');
const CostIcon = ({ className, t }) => {
    return React.createElement("img", { className: `cost-icon ${className}`, src: icon, alt: t('cost_management'), "aria-hidden": "true" });
};
export default withTranslation()(CostIcon);
